import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { useEffect, useState} from 'react';
import logo from '../assets/img/logo.svg';
import navIcon1 from '../assets/img/nav-icon1.svg';
import navIcon2 from '../assets/img/nav-icon2.svg';
import navIcon3 from '../assets/img/nav-icon3.svg';

export const NavBar = () => {
    const [activeLink, setActiveLink] = useState('home');
    const [scrolled, setScrolled] = useState(false);

    // Define the onScroll function
    const onScroll = () => {
        if (window.scrollY > 50) {
            setScrolled(true);
        } else {
            setScrolled(false);
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', onScroll);
        return () => window.removeEventListener('scroll', onScroll);
    }, []);

    const onUpdateActiveLink = (value) => {
        setActiveLink(value);
    };

    return (
        <Navbar expand="lg" className={scrolled ? 'scrolled' : ''}>
            <Container>
                <Navbar.Brand href="#home">
                    <img src={logo} alt="Logo" />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav">
                    <span className="navbar-toggler-icon"></span>
                </Navbar.Toggle>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link
                            href="#home"
                            className={
                                activeLink === 'home' ? 'active navbar-link' : 'navbar-link'
                            }
                            onClick={() => onUpdateActiveLink('home')}
                        >
                            Home
                        </Nav.Link>
                        <Nav.Link
                            href="#project"
                            className={
                                activeLink === 'projects' ? 'active navbar-link' : 'navbar-link'
                            }
                            onClick={() => onUpdateActiveLink('projects')}
                        >
                            Projects
                        </Nav.Link>
                        <Nav.Link
                            href="#about"
                            className={
                                activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'
                            }
                            onClick={() => onUpdateActiveLink('about')}
                        >
                            About
                        </Nav.Link>
                    </Nav>
                    <span className="navbar-text">
                    <div className="social-icon">
                        <a href="mailto:lalithadityasureshkumar@gmail.com">
                            <img src={navIcon1} alt="Email Lalith" />
                        </a>
                        <a href="https://github.com/lalithadsuresh" target="_blank" rel="noopener noreferrer">
                            <img src={navIcon2} alt="GitHub Lalith" />
                        </a>
                        <a href="https://www.linkedin.com/in/lalithsuresh/" target="_blank" rel="noopener noreferrer">
                            <img src={navIcon3} alt="LinkedIn Lalith" />
                        </a>
                    </div>
                    </span>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};